<template>
    <div v-if="isVisible" 
    :class="['notification-bar', { 'scrolled': isScrolled }]"
     ref="notificationBar">
    
      <slot>
        <span class="notification-text">
          Enjoy up to <span class="highlight">25% OFF</span> on all products!
        </span>
        <a  @click="scrollTo('TopProducts')" class="shop-link">Shop Now →</a>
      </slot>
      <button v-if="closable" @click="closeBar" class="close-btn">×</button>
    </div>
  </template>
  
  <script>
  
  //mixins
  import { scrollMixin } from '@/mixins/scrollMixin';

  export default {
    name: "NotificationBar",
    mixins: [scrollMixin],

    props: {
      closable: {
        type: Boolean,
        default: true,
      },
    },
    data() {
      return {
        isVisible: true,
      };
    },
    methods: {
      closeBar() {
        this.isVisible = false;
      },
      
    },
    mounted() {

    window.addEventListener("scroll", this.handleScroll);
    },

    beforeDestroy() {
    window.removeEventListener("scroll", this.handleScroll);
    },
  };
  </script>
  
  <style scoped>
  .notification-bar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: linear-gradient(90deg, #56ab2f, #a8e063);
    color: #fff;
    padding: 0 20px;
    height: 50px;
    font-size: 1rem;
    font-family: "Arial", sans-serif;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.15);
    /* border-radius: 5px; */
    overflow: hidden;
    white-space: nowrap;
  }
  .notification-bar.scrolled {
    position: relative;
     z-index: 1000; 
}
  /* Text   */
  .notification-text {
    font-weight: 600;
    margin-right: 10px;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  
  /* Spichial Text  */
  .highlight {
    color: #ffe600; 
    font-weight: bold;
  }
  
  /* Links */
  .shop-link {
    text-decoration: none;
    color: #ffffff;
    background-color: #ff5722; 
    padding: 5px 15px;
    font-weight: bold;
    border-radius: 3px;
    margin-right: auto;
    transition: background-color 0.3s ease, color 0.3s ease;
  }
  
  .shop-link:hover {
    background-color: #ff8a50;
    color: #000;
  }
  
  /* close btn*/
  .close-btn {
    background: none;
    border: none;
    font-size: 1.2rem;
    cursor: pointer;
    color: #fff;
    margin-right: 10px;
    transition: color 0.3s ease;
  }
  
  .close-btn:hover {
    color: #ffe600;
  }
  
  @media (max-width: 768px) {
    .notification-bar {
      font-size: 0.9rem;
      padding: 0 15px;
    }
    .close-btn {
      font-size: 1rem;
    }
  }
  </style>
  